import React from 'react';
import Deletebutton from './Deletebutton';

const TableBody = (props) => {
    console.log("ey aca", props);
    return (
        <tbody>
        <tr>
       
          <td>{props.info[0].name}</td>
          <td>{props.info[0].job}</td>
          <td><Deletebutton/></td>
        </tr>
        <tr>
         
          <td>{props.info[1].name}</td>
          <td>{props.info[1].job}</td>
          <td><Deletebutton/></td>
        </tr>
        <tr>
         
          <td>{props.info[2].name}</td>
          <td>{props.info[2].name}</td>
          <td><Deletebutton/></td>
        </tr>
      </tbody>
    );
};

export default TableBody;